.form-full-layout {
    display: flex;
    flex-direction: column;
    margin: 10px;
    gap: 1rem;
}

/* 스크롤바 숨기기 (모던 브라우저) */
.hide-scrollbar {
    overflow: auto;
    /* 스크롤은 유지 */
}

.hide-scrollbar::-webkit-scrollbar {
    width: 0px;
    background: transparent;
    /* 웹킷 기반 브라우저 (크롬, 사파리)에서 스크롤바 숨기기 */
}

.hide-scrollbar {
    -ms-overflow-style: none;
    /* IE and Edge에서 스크롤바 숨기기 */
    scrollbar-width: none;
    /* Firefox에서 스크롤바 숨기기 */
}